.InfoTooltip__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 36px;
    background: #ffffff;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    min-width: 328px;
    position: relative;
  }
  
  @media screen and (max-width: 580px) {
    .InfoTooltip__container {
      padding: 22px;
      min-width: 240px;
    }
  }