.popup {
  visibility: hidden;
  opacity: 0;
  transition: 0.3s;
  display: flex;
  position: fixed;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  left: 0;
  right: 0;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  z-index: 1;
}
